html, body {
  margin:0;
  padding:0;
  min-height:100%;
  width:100%;
  box-sizing:border-box;
  background-color:#f0f4f9 !important;
}

#container {
  min-height:100%;
}

// Remove padding on tablet devices
@media only screen and (min-device-width: 1112px) {

  #container { 
    padding:20px;
  }
   
  #app {
    border-radius:3px;
  }

}

#app {
  max-width:1400px;
  margin:0 auto;
  overflow:hidden;
  background-color:#fff;
  box-shadow:0 0 4px rgba(0,0,0,0.05);

  nav {
    width:100%;
    background-color:#af5030;    
    padding:10px 16px 7px 20px;
    color:#fff;
    text-align:right;
    background-position:-1px -1px;
    background-repeat:no-repeat;
    background-size:292px 65px;

    .nav-item-wrapper {
      display:inline-block;
      position:relative;
      padding:0 10px 0 20px;
    }

    .nav-item {
      display:flex;
      flex-direction:column;
      cursor:pointer;
      color:#fff;

      a {
        text-decoration:none;
      }

      * {
        display:block;
        text-align:center;
      }

      .anticon {
        display:inline-block;
        font-size:20px;
      }

      label {
        padding-top:3px;
        font-size:12px;
      }

    }
  }

  a {
    color:#d23500;
    text-decoration:underline;
  }

}

